import React, { useEffect, useRef, useState } from 'react'
import { observer } from 'mobx-react'
import ReactPlayer from 'react-player'
import cx from 'classnames'

import store, { Cut } from '../../store'
import styles from './CutItem.module.scss'
import closeIcon from './assets/img/close.svg'
import playerService from '../../services/playerService'
import playIcon from './assets/img/playIcon.svg'

interface Props {
	cut: Cut
	idx: number
	isActive: boolean
	isChosenSource: boolean
	onClick: (idx: number) => void
}

const CutItem: React.FC<Props> = ({ cut, idx, isActive, isChosenSource, onClick }) => {
	const blockRef: React.RefObject<HTMLDivElement> = useRef(null)
	const playerRef = useRef(null)
	const [isVisible, setIsVisible] = useState(false)
	const remove = () => store.removeCut(idx)
	const play = () => playerService.playOne(idx)

	const playHandler = () => {
		const player = (playerRef.current as any).player.player.player
		if (player?.playVideo) player.playVideo()
	}

	const stopHandler = () => {
		const player = (playerRef.current as any).player.player.player
		if (player?.pauseVideo) player.pauseVideo()
	}

	const onEndHandler = (start: number) => (playerRef.current as any).player.player.player.seekTo(start)

	useEffect(() => {
		const { current } = blockRef
		if (current) {
			current.addEventListener('mouseover', () => {
				setIsVisible(true)
				playHandler()
			})
			current.addEventListener('mouseout', () => {
				setIsVisible(false)
				stopHandler()
			})
		}

		return () => {
			if (current) {
				current.removeEventListener('mouseover', () => {
					playHandler()
				})
				current.removeEventListener('mouseout', () => {
					stopHandler()
				})
			}
		}
	}, [blockRef])

	return (
		<div
			className={cx(styles.wrapper, {
				[styles.isChosenSource]: isChosenSource,
			})}
		>
			<div className={styles.buttons}>
				<div className={styles.checkboxWrapper}>
					<input
						id={idx.toString()}
						type='checkbox'
						checked={isActive}
						className={styles.checkbox}
						onChange={() => onClick(idx)}
					/>
					<label htmlFor={idx.toString()} />
				</div>
				<button onClick={remove}>
					<img src={closeIcon} alt='Close icon' />
				</button>
			</div>
			<div className={styles.playerWrapper} ref={blockRef}>
				<ReactPlayer
					key={`${cut.startTime} ${cut.endTime}`}
					ref={playerRef}
					width='100%'
					height='100%'
					url={store.cuts[idx].url}
					muted
					controls={false}
					onEnded={() => onEndHandler(cut.startTime)}
					config={{
						youtube: {
							playerVars: {
								controls: 0,
								mute: 1,
								showinfo: 0,
								modestbranding: 1,
								start: Math.floor(cut.startTime),
								end: Math.floor(cut.endTime),
							},
						},
					}}
				/>
				<button
					onClick={play}
					className={cx(styles.playButton, {
						[styles.isVisible]: isVisible,
					})}
				>
					<img src={playIcon} alt='Play icon' />
				</button>
			</div>
		</div>
	)
}

export default observer(CutItem)
