import store, { Cut, Gif } from '../store'
import playerService from './playerService'

class GifService {
	getCutTime(gifId: string) {
		let cutTime: Cut = {
			startTime: 0,
			endTime: 0,
		}
		const gifIndex = this.getGifIndexById(gifId)

		if (gifIndex !== null) {
			const cutId = store.gifs[gifIndex].cutId

			store.cuts.map((item) => {
				if (item.id === cutId) cutTime = item.cut
			})
		}

		return cutTime
	}

	setGifTime(gifId: string, value: any) {
		const gifIndex = this.getGifIndexById(gifId)

		if (gifIndex === null) return

		store.gifs[gifIndex].startShowTime = value[0]
		store.gifs[gifIndex].endShowTime = value[1]
	}

	getGifIndexById(gifId: string) {
		let index = null

		store.gifs.map((item) => {
			if (item.id === gifId) index = item.index
		})

		return index
	}

	getGifTime(gifId: string) {
		let time = [0, 0]
		const index = this.getGifIndexById(gifId)

		if (index !== null) {
			time = [store.gifs[index].startShowTime, store.gifs[index].endShowTime]
		}

		return time
	}

	isShowGif(gif: Gif) {
		if (store.cuts[store.currentCutIndex] === undefined || gif.cutId !== store.cuts[store.currentCutIndex].id)
			return false

		const currentPosition = playerService.getActivePlayerPosition()

		return currentPosition >= gif.startShowTime && currentPosition <= gif.endShowTime
	}

	checkShowGifs(position: number) {
		if (!store) return

		for (let i = 0; i < store.gifs.length; i += 1) {
			const gif = store.gifs[i]

			if (store.cuts[store.currentCutIndex] === undefined) {
				store.gifs[i].isShow = false
				continue
			}
			if (
				gif.cutId === store.cuts[store.currentCutIndex].id &&
				gif.cutId === store.cuts[store.currentCutIndex].id &&
				position >= gif.startShowTime &&
				position < gif.endShowTime
			) {
				if (!store.gifs[i].isShow) console.log('Show gif on position: ' + position)
				store.gifs[i].isShow = true
			} else {
				if (store.gifs[i].isShow) console.log('Hide gif on position: ' + position)
				store.gifs[i].isShow = false
			}
		}
	}

	deleteGif(gifId: string) {
		store.gifs = store.gifs.filter((gif) => gif.id !== gifId)
	}
}

const gifService = new GifService()

export default gifService
