import React, { useEffect, useState } from 'react'
import { ButtonGroup, IconButton, Slider } from '@material-ui/core'
import DeleteIcon from '@material-ui/icons/Delete'
import PlayArrowIcon from '@material-ui/icons/PlayArrow'
import ThumbUpIcon from '@material-ui/icons/ThumbUp'
import { observer } from 'mobx-react'

import store from '../../store'
import gifService from '../../services/gifService'
import styles from './GifMenu.module.scss'
import playerService from '../../services/playerService'

interface Props {}

const GifMenu: React.FC<Props> = (props) => {
	const [value, setValue] = useState([0, 0])

	useEffect(() => {
		if (store.selectedGifId === null) return
		setValue(gifService.getGifTime(store.selectedGifId))
	}, [store.selectedGifId])

	const onChangeGifTime = (event: React.ChangeEvent<{}>, newValue: any) => {
		setValue(newValue)
	}

	const saveTime = () => {
		if (store.selectedGifId === null) return
		gifService.setGifTime(store.selectedGifId, value)
	}

	const deleteGif = () => {
		if (store.selectedGifId === null) return
		gifService.deleteGif(store.selectedGifId)
		store.selectedGifId = null
	}

	const playCut = async () => {
		await playerService.playOne(store.currentCutIndex)
	}

	return (
		<>
			{store.selectedGifId && (
				<div className={styles.container}>
					<Slider
						value={value}
						onChange={onChangeGifTime}
						valueLabelDisplay='auto'
						aria-labelledby='range-slider'
						max={gifService.getCutTime(store.selectedGifId).endTime}
						min={gifService.getCutTime(store.selectedGifId).startTime}
						step={0.01}
					/>
					<ButtonGroup size='small' aria-label='small outlined button group'>
						<IconButton aria-label='play' color='primary' onClick={playCut}>
							<PlayArrowIcon />
						</IconButton>
						<IconButton aria-label='thumbUp' color='primary' onClick={saveTime}>
							<ThumbUpIcon />
						</IconButton>
						<IconButton aria-label='delete' color='primary' onClick={deleteGif}>
							<DeleteIcon />
						</IconButton>
					</ButtonGroup>
				</div>
			)}
		</>
	)
}

export default observer(GifMenu)
